<template>
<div class="modal__req d-flex justify-content-center align-items-center">
    <div class="modal__req__content d-flex">
        <div class="request__form">
            <form class="d-flex flex-column align-items-start reqForm">
                <div class="d-flex"><h1></h1><span class="some__span1">     </span></div>

                <div class="row1 d-flex justify-content-between">
                    <div class="st d-flex align-items-center">
                        <span class="some__span2">, , .</span>
                        <input class="medium__text" type="text" v-model="newReq.address">
                    </div>
                    <div class="st d-flex align-items-center">
                        <span class="some__span2"></span>
                        <input class="medium__text" type="text" v-model="newReq.city" placeholder="">
                    </div>
                </div>

                <h1 style="margin-top: 20px"></h1>

                <div class="row1 d-flex align-items-center">
                    <span class="some__span2">C</span>
                    <input class="little__text" type="text" id="date__mask" v-model="newReq.date" placeholder="">
                    <input class="little__text" style="margin-right: 32px" type="text" id="time__mask" v-model="starttime" placeholder="">
                    <span class="some__span2"></span>
                    <input class="little__text"  type="text" id="date__mask2" v-model="newReq.endin" placeholder="">
                    <input class="little__text" type="text" id="time__mask2" v-model="endtime" placeholder="">
                </div>

                <h1 style="margin-top: 20px"></h1>
                <input class="max__text" v-model="newReq.task" type="text">

                <h1 style="margin-top: 20px"></h1>
                <select class="max__text" v-model="newReq.form" style="cursor: pointer">
                    <option :value="forms[0].name">{{ forms[0].name }} ( )</option>
                    <option v-for="form in forms" :value="form.name" v-if="form.name != forms[0].name">{{ form.name }}</option>
                </select>

                <h1 style="margin-top: 20px"></h1>
                <select class="max__text" v-model="newReq.executor" style="cursor: pointer">
                    <option value="-">  ( )</option>
                    <option v-for="executor in executors" :value="executor.name">{{ executor.name }}</option>
                </select>

                <h1 style="margin-top: 20px">CRM-</h1>
                <input class="max__text" type="text" v-model="newReq.crm" >

                <h1 style="margin-top: 20px"></h1>
                <textarea class="max__text" v-model="newReq.instructions" style="height: 67px" wrap="soft"></textarea>
            </form>
            <div class="req__modal__footer d-flex justify-content-center">
                    <button class="req__btn" @click="showReForm"></button>
                    <button class="req__btn" @click="addNewReq"></button>
                </div>
        </div>
        <div class="request__map"></div>
    </div>
</div>
</template>

<script>
import Inputmask from 'inputmask';

export default {
    props: ["forms", "executors", "isForm", "requests"],

    data: function() {
        return {
            all: '',
            localIsForm: this.isForm,
            localReqs: this.requests,
            startdate: "",
            starttime: "",
            enddate: "",
            endtime: "",
            newReq: {
                id: "",
                status: "",
                city: "",
                address: "",
                date: this.startdate,
                endin: this.endtdate,
                executor: "",
                formColor: "",
                form: this.forms[0].name,
                task: "",
                crm: "",
                instructions: "",
            },
        }
    },

    mounted: function() {
        var im = new Inputmask("99/99/9999");
        im.mask(document.getElementById('date__mask'));
        var im = new Inputmask("99:99");
        im.mask(document.getElementById('time__mask'));
        var im = new Inputmask("99/99/9999");
        im.mask(document.getElementById('date__mask2'));
        var im = new Inputmask("99:99");
        im.mask(document.getElementById('time__mask2'));
    },

    methods: {
        showReForm: function() {
            this.localIsForm = false;
            this.$emit('showReForm', this.localIsForm)
        },

        addNewReq: function() {
            if (this.newReq.city == ''){console.log('  ');}
            else if (this.newReq.address == ''){console.log('  ');}
            else if (this.newReq.task == ''){console.log('  ');}
            else if (this.newReq.form == ''){console.log('  ');}
            else if (this.newReq.executor == ''){console.log('  ');}
            else {
                var bg = '';
                this.newReq.id = Math.random() * (9999999 - 1) + 1;
                bg = Math.floor(Math.random() * 899) + 100;
                this.forms.forEach((val, i) => {
                    if (val.name == this.newReq.form) {
                    this.newReq.color = val.color;
                    }
                });
                this.localReqs.push(this.newReq);
                this.$emit('addNewRequest', this.localReqs)
                this.localIsForm = false;
                this.$emit('showReForm', this.localIsForm)
            }
        },
    },

}
</script>

<style>

.modal__req {
    position: absolute;
    width: 100%;
    min-width: 1240px;
    height: calc(100% - 44px);
    z-index: 1050;
    background: rgba(0, 0, 0, 0.3);
}

.modal__req__content {
    position: relative;
    width: 1100px;
    height: auto;
    background: #FFF;
    border: 1px solid #000000;
}

.request__map {
    width: 550px;
    background: url("./assets/map_modal.png") no-repeat;
    background-size: 550px 100%;
}

.request__form {
    width: 550px;
    padding: 20px 30px 20px 38px;
}

.reqForm {
    width: 100%;
    height: auto;
}

.reqForm h1 {
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
}

.some__span1 {
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    color: #9E9E9E;
    margin-left: 14px;
}

.reqForm h1 {
    padding: 0;
}

.row1 {
    width: 100%;
}

.some__span2 {
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    margin-right: 4px;
}

.medium__text, .little__text, .max__text {
    width: 150px;
    height: 30px;
    background: #E9E9E9;
    border-radius: 5px;
    border: 0;
    padding-left: 13px;
    outline: none;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
}

.little__text {
    width: 73px;
    text-align: center;
    margin-left: 24px;
    padding: 0;
}

.max__text {
    width: 480px;
}

.req__modal__footer {
    width: 100%;
    margin-top: 20px;
}

.req__btns {
    max-width: 181px;
    height: auto;
}

.req__btn {
    font-size: 9px;
    font-weight: 300;
    border: 0;
    background: #FFD79A;
    border-radius: 5px;
    width: 100px;
    height: 28px;
    margin: 0px 10px;
    outline: none;
}

.req__btn:hover {
    background: #FFE6C0;
    transition: .3s;
}

</style>


